export default {
  name: "aimg",
  props: ["iid"],
  data: function() {
    return {
      wg: {},
      ns: {},
      kj: {},
      tj: {},
      gf: {}
    };
  },
  methods: {
    bimg(aid, bid, cid) {
      // console.log(aid)
      // console.log(bid)
      // console.log(cid)
      this.$router.push("/bimg/" + aid + "/" + bid);
    }
  },
  beforeRouteEnter: function(to, from, next) {
    next(function(vm) {
      vm.$http
        .get("/v2-car-getImageList.html?SerialID=" + to.params.iid)
        .then(function(response) {
          console.log(response.data.data);
          vm.wg = response.data.data[0];
          vm.ns = response.data.data[1];
          vm.kj = response.data.data[2];
          vm.tj = response.data.data[3];
          vm.gf = response.data.data[4];
          // console.log(vm.ns)
        });
    });
  },
    beforeRouteUpdate: function(to, from, next) {
    
    var app = this; 
    //重新发送ajax请求
    this.$http.get("/v2-car-getImageList.html?SerialID="+ to.params.iid).then(function(recursion) {
    
          app.wg = response.data.data[0];
          app.ns = response.data.data[1];
          app.kj = response.data.data[2];
          app.tj = response.data.data[3];
          app.gf = response.data.data[4];
        
      });
    // 执行下一步
    next();
  }
};



