import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../components/Home.vue'
import Content from '../components/Content.vue'
import Price from '../components/Price.vue'
import Img from '../components/Img.vue'
import Nimg from '../components/Nimg.vue'
Vue.use(VueRouter)

const routes=[
   // { path:'/',name:'home', component:Home},
  //  {path:'/',name:'test',component:Test,props:true},
   {path:'/',name:'home',component:Home,props:true},
   {path:'/content/:id',name:'content',component:Content,props:true},
   {path:'/price/:nid',name:'price',component:Price,props:true},
   {path:'/bimg/:aid/:bid',name:'nimg',component:Nimg,props:true},
   {path:'/aimg/:iid',name:'aimg',component:Img,props:true}
 ];
 //配置路由管理器
 const router = new VueRouter({
   routes
 });
 
 export default router;



