
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Card from 'vux/src/components/card/index.vue'

export default {
name:'content',
props:['id'],
components: {
CellBox,
Group,
Flexbox,
FlexboxItem,
Card,
Cell
},
data:function(){
return{
carall:'',
lists:[]
}
},
methods:{
xwdj:function(id){
this.$router.push('/price/'+id)
},
nimg:function(id){
this.$router.push('/aimg/'+id)
},
quanbu:function(id){
var app=this;
this.$http.get("/v2-car-getInfoAndListById.html?SerialID="+id).then(function(response){
app.lists=response.data.data.list;
})
},
quanbu1:function(id){
var app=this;
this.$http.get("/v2-car-getInfoAndListById.html?SerialID="+id).then(function(response){
var listtest=[];
for(var i=0;i<response.data.data.list.length;i++){
if(response.data.data.list[i].market_attribute.year=='2019'){
listtest.push(response.data.data.list[i]);
}
}
app.lists=listtest;

})
},
quanbu2:function(id){
var app=this;
this.$http.get("/v2-car-getInfoAndListById.html?SerialID="+id).then(function(response){
var listtest=[];
for(var i=0;i<response.data.data.list.length;i++){
if(response.data.data.list[i].market_attribute.year=='2018'){
listtest.push(response.data.data.list[i]);
}
}
app.lists=listtest;

})
},

},
beforeRouteEnter: function(to, from, next) {
next(function(vm) {
if(vm.carall==''){
vm.$http.get("/v2-car-getInfoAndListById.html?SerialID="+to.params.id).then(function(response) {
vm.carall=response.data.data;
vm.lists=response.data.data.list;

});
}
});
}
}



