
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Card from 'vux/src/components/card/index.vue'
import Popup from 'vux/src/components/popup/index.vue'

export default {
name: "home",
data: function() {
return {
lista: [],
listb: [],
listc: [],
listd: [],
liste: [],
listf: [],
listg: [],
listh: [],
listi: [],
listj: [],
listk: [],
listl: [],
listm: [],
listn: [],
listo: [],
listp: [],
listq: [],
listr: [],
lists: [],
listt: [],
listv: [],
listw: [],
listu: [],
listx: [],
listy: [],
listz: [],
cars: [],
show:false
};
},
components: {
CellBox,
Group,
Flexbox,
FlexboxItem,
Card,
Cell,
Popup
},
methods: {
open: function(id) {
var app = this;
this.$http.get(
"https://baojia.chelun.com/v2-car-getMakeListByMasterBrandId.html?MasterID=" +
id
).then(function(response) {
app.cars = response.data.data;

});
if(app.show==false){
app.show=true
}else{
app.show=false
}








}
},
beforeRouteEnter: function(to, from, next) {
next(function(vm) {
vm.$http.get("/v2-car-getMasterBrandList.html").then(function(response) {
for (var i = 0; i < response.data.data.length; i++) {
if (response.data.data[i].Spelling.substr(0, 1) == "A") {
vm.lista.push(response.data.data[i]);
}
if (response.data.data[i].Spelling.substr(0, 1) == "B") {
vm.listb.push(response.data.data[i]);
}
if (response.data.data[i].Spelling.substr(0, 1) == "C") {
vm.listc.push(response.data.data[i]);
}
if (response.data.data[i].Spelling.substr(0, 1) == "D") {
vm.listd.push(response.data.data[i]);
}
if (response.data.data[i].Spelling.substr(0, 1) == "E") {
vm.liste.push(response.data.data[i]);
}
if (response.data.data[i].Spelling.substr(0, 1) == "F") {
vm.listf.push(response.data.data[i]);
}
if (response.data.data[i].Spelling.substr(0, 1) == "G") {
vm.listg.push(response.data.data[i]);
}
if (response.data.data[i].Spelling.substr(0, 1) == "H") {
vm.listh.push(response.data.data[i]);
}
if (response.data.data[i].Spelling.substr(0, 1) == "I") {
vm.listi.push(response.data.data[i]);
}
if (response.data.data[i].Spelling.substr(0, 1) == "J") {
vm.listj.push(response.data.data[i]);
}
if (response.data.data[i].Spelling.substr(0, 1) == "K") {
vm.listk.push(response.data.data[i]);
}
if (response.data.data[i].Spelling.substr(0, 1) == "L") {
vm.listl.push(response.data.data[i]);
}
if (response.data.data[i].Spelling.substr(0, 1) == "M") {
vm.listm.push(response.data.data[i]);
}
if (response.data.data[i].Spelling.substr(0, 1) == "N") {
vm.listn.push(response.data.data[i]);
}
if (response.data.data[i].Spelling.substr(0, 1) == "O") {
vm.listo.push(response.data.data[i]);
}
if (response.data.data[i].Spelling.substr(0, 1) == "P") {
vm.listp.push(response.data.data[i]);
}
if (response.data.data[i].Spelling.substr(0, 1) == "Q") {
vm.listq.push(response.data.data[i]);
}
if (response.data.data[i].Spelling.substr(0, 1) == "R") {
vm.listr.push(response.data.data[i]);
}
if (response.data.data[i].Spelling.substr(0, 1) == "S") {
vm.lists.push(response.data.data[i]);
}
if (response.data.data[i].Spelling.substr(0, 1) == "T") {
vm.listt.push(response.data.data[i]);
}
if (response.data.data[i].Spelling.substr(0, 1) == "V") {
vm.listv.push(response.data.data[i]);
}
if (response.data.data[i].Spelling.substr(0, 1) == "U") {
vm.listu.push(response.data.data[i]);
}
if (response.data.data[i].Spelling.substr(0, 1) == "W") {
vm.listw.push(response.data.data[i]);
}
if (response.data.data[i].Spelling.substr(0, 1) == "X") {
vm.listx.push(response.data.data[i]);
}
if (response.data.data[i].Spelling.substr(0, 1) == "Y") {
vm.listy.push(response.data.data[i]);
}
if (response.data.data[i].Spelling.substr(0, 1) == "Z") {
vm.listz.push(response.data.data[i]);
}
}
});
});
}
};



